import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, App, Platform } from 'ionic-angular';
import { LaunchNavigator } from '@ionic-native/launch-navigator';

import { Observable } from 'rxjs/Observable';

import { LoopBackConfig } from '../../../shared/sdk';
import { SqliteServiceProvider } from '../../providers/sqlite-service/sqlite-service';

declare const lbclient: any;

import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { SiteconfigServiceProvider } from '../../providers/siteconfig-service/siteconfig-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { GpsServiceProvider } from '../../providers/gps-service/gps-service';
import { PlatformReadyServiceProvider } from '../../providers/platform-ready-service/platform-ready-service';
import { ExitServiceProvider } from '../../providers/exit-service/exit-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';
import { DeviceServiceProvider } from '../../providers/device-service/device-service';
import { MessageServiceProvider } from '../../providers/message-service/message-service';
import { JobServiceProvider } from '../../providers/job-service/job-service';
import { JseaServiceProvider } from '../../providers/jsea-service/jsea-service';
import { SodServiceProvider } from '../../providers/sod-service/sod-service';
import { PushServiceProvider } from '../../providers/push-service/push-service';
import { ConnectivityMonitorProvider } from '../../providers/connectivity-monitor/connectivity-monitor';
import { NavigationServiceProvider } from '../../providers/navigation-service/navigation-service';
import { LogServiceProvider } from '../../providers/log-service/log-service';


import { MyApp } from '../../app/app.component';
import { PdaPage } from '../pda/pda'

/**
 * Generated class for the DevicePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
interface ClientConfig {
  "connector": "",
  "debug": "",
  "clientId": "",
  "serverIP": "",
  "serverPort": 0,
  "url": ""
}

@IonicPage()
@Component({
  selector: 'page-device',
  templateUrl: 'device.html',
})
export class DevicePage {

  username: any;
  clientConfig: ClientConfig;
  appConfig;
  version;
  build;
  //fullreplication;
  usedStorage;
  localStorageList;
  sqliteStorageList;
  error;
  device;
  //isOnline;
  //networkType;
  driverId: number;
  newMessageCount: number;
  loginFlag = this.pdaParams.isDrvLoggedOn();
  devicectrl = {
    title: '',
    drivernum: 0,
    isGpsWorking: false,
    isDeviceReady: false,
    showGpsAlerts: false,
    sendGps: false,
    registeredForPush: false
  };
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'DevicePage' };
  log = this.logger.getInstance(this.logParams);
  def_gps_stationaryRadius;
  def_gps_stopOnStillActivity;
  def_gps_activitiesInterval;
  def_gps_fastestInterval;
  def_gps_stopOnTerminate;
  def_gps_interval;
  def_gps_distanceFilter;
  availableMapApps = [];
  favouriteMapApp;
	def_signature_throttle;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private platformReady: PlatformReadyServiceProvider,
    private configService: ConfigServiceProvider, private logger: LogServiceProvider,
    private pdaParams: PdaparamsServiceProvider, private gpsService: GpsServiceProvider,
    private exitService: ExitServiceProvider, private deviceService: DeviceServiceProvider,
    private sharedService: SharedServiceProvider, public storage: SqliteServiceProvider,
    private alertCtrl: AlertController, private siteConfig: SiteconfigServiceProvider,
    private messageService: MessageServiceProvider, private jobService: JobServiceProvider,
    private jseaService: JseaServiceProvider, private sodService: SodServiceProvider,
    private conMonService: ConnectivityMonitorProvider, private pushService: PushServiceProvider,
    private navigatorService: NavigationServiceProvider, private navigatorPlugin: LaunchNavigator,
    private app: App, public platform: Platform) {
    this.username = navParams.data;
    //console.log("DevicePage loading...");

    LoopBackConfig.setBaseURL(configService.apiURL);
    LoopBackConfig.setApiVersion(configService.API_VERSION);
    this.clientConfig = this.configService.getClientConfig();
    this.appConfig = this.configService.appConfig;

    this.conMonService.isConnected.subscribe(res => {
      this.sharedService.isOnline = res;
    });
    /*  this.conMonService.networkType.subscribe(res => {
       this.sharedService.networkType = res;
     }); */


    //Store GPS Defaults
    this.def_gps_stationaryRadius = pdaParams.gps_stationaryRadius;
    this.def_gps_stopOnStillActivity = pdaParams.gps_stopOnStillActivity;
    this.def_gps_fastestInterval = pdaParams.gps_fastestInterval;
    this.def_gps_stopOnTerminate = pdaParams.gps_stopOnTerminate;
    this.def_gps_interval = pdaParams.gps_interval;
    this.def_gps_distanceFilter = pdaParams.gps_distanceFilter;
    this.def_gps_activitiesInterval = pdaParams.gps_activitiesInterval;

		this.def_signature_throttle = this.siteConfig.getSiteConfigInt('PDA_SIGNATURE_THROTTLE') || 16;
  }//Constructor ends

  ionViewWillEnter() {

    /*    this.storage.sqliteStorageList().then((rows: any) => {
         this.sqliteStorageList = rows;
   
         for (let i = 0; i < this.sqliteStorageList.length; i++) {
           console.log("item: " + this.sqliteStorageList(i).item + ", ItemValue: " + this.sqliteStorageList(i).itemvalue);
           console.log("-------======================----------");
         }
   
       }); */

    //set the tab string in shared-service
    this.sharedService.tab = "Device";
    this.log.info("In the Device Tab.");


    this.driverId = this.pdaParams.getDriverId();
    this.devicectrl.title = "Device Details" + " v" + this.configService.appConfig.version;
    this.devicectrl.drivernum = this.pdaParams.getDriverId();
    this.devicectrl.isGpsWorking = this.gpsService.getGpsIsWorking();
    this.devicectrl.isDeviceReady = this.platformReady.isPlatformReady();
    this.devicectrl.showGpsAlerts = this.gpsService.getShowGpsAlerts();
    this.devicectrl.sendGps = this.gpsService.getSendGps();

    //if (this.devicectrl.isDeviceReady) {
      this.devicectrl.registeredForPush = this.pushService.getIsRegistered();
      this.version = this.configService.appConfig.version;
      this.build = this.configService.appConfig.build;
      //this.fullreplication = lbclient.fullreplication.fullReplication;
      this.device = this.deviceService.getDevice();
    //}

    this.usedStorage = Math.round(JSON.stringify(localStorage).length / 1024) + " KB";
    this.localStorageList = this.localStorageUsage();

    /*  this.navigatorPlugin.appSelection.userChoice.get((app) => {
       this.favouriteMapApp = this.navigatorPlugin.getAppDisplayName(app);
     }); */

    if(!this.sharedService.desktopTesting)
    {
        this.navigatorPlugin.availableApps().then((results) => {
        this.availableMapApps = [];		// reset otherwise the list keeps on growing
          for (var app in results) {
            this.availableMapApps.push(this.navigatorPlugin.getAppDisplayName(app) + (results[app] ? " is" : " isn't") + " available");
          }
        });
    }



  }// ionViewWillEnter() ends

  showResult = () => {
    return this.error == "";
  }

  updateShowGpsAlerts = (torf) => {
    this.gpsService.setShowGpsAlerts(torf);
  }

  updateSendGps = (torf) => {
    this.gpsService.setSendGps(torf);
  }

  exitTplusMobile = () => {
    this.gpsService.setSendGps(false);
    this.exitService.exitTplusMobile();
  }

  toggleDesktopBrowserScrolling = () => {
    this.sharedService.setDesktopBrowserScrolling(!this.sharedService.setDesktopBrowserScrolling);
  }

  pushRegister = () => {
    this.pushService.registerForPush();
  }

  clearNavUserChoice = () => {
    this.navigatorService.clearUserChoice();
  }

  resetApp = () => {
    let confirmPopup = this.alertCtrl.create({
      title: 'Reset App',
      message: 'This will delete all data for this app and you will need to login again.  Are you sure you want to do this?',
      buttons: [
        {
          text: 'Yes',
          handler: () => {
            localStorage.clear();
            //if (!this.sharedService.desktopTesting) {
              this.storage.clear();

              if (this.platform.is('cordova') && (<any>window).sqlitePlugin)
                this.storage.closeDB();
            //}

            // TODO -  clear images if required - see /app/strongloop/loopback-clientservices-osc-images/client/js/deviceCtrl.js           
            this.app.getRootNav().setRoot(MyApp);

            //document.location.href = 'index.html';
            //return false;
          }
        },
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            //console.log('Cancel clicked');
            return;
          }
        }
      ],
      enableBackdropDismiss: false
    });
    confirmPopup.present();
  }

  reloadSiteConfig = () => {
    this.siteConfig.deleteLocalConfigs();
    this.log.info("Local Configs deleted!");
    var result$: Observable<string> = this.siteConfig
      .loadAllConfigsFromServer()
      .subscribe((result) => {
        this.log.debug("Site Config Reloaded!");

        let alert = this.alertCtrl.create({
          title: 'Site Config',
          subTitle: 'Site Config Reloaded...',
          buttons: ['OK'],
          enableBackdropDismiss: false
        });
        alert.present();

        this.ionViewWillEnter();
      },
        (error) => {
          this.showError("Unable to load Site Configs.Please check your connection.");
          return;
        });


  }

  showError(text) {
    let alert = this.alertCtrl.create({
      title: 'Error',
      subTitle: text,
      buttons: ['OK'],
      enableBackdropDismiss: false
    });
    alert.present();
  }

  clearSqliteStorage = () => {
    let confirmPopup = this.alertCtrl.create({
      title: 'Clear Sqlite Storage',
      message: 'This will clear all data from Sqlite Storage.  Are you sure you want to do this?',
      buttons: [
        {
          text: 'Yes',
          handler: () => {
            if (!this.sharedService.desktopTesting) {
              this.storage.clear().then((val) => {
                // TODO -  clear images if required - see /app/strongloop/loopback-clientservices-osc-images/client/js/deviceCtrl.js   
                if (val)
                  this.app.getRootNav().setRoot(MyApp);
                else {
                  console.log("Error in clearing the sqlite storage:" + val);
                  this.log.error("Error in clearing the sqlite storage:" + val);
                }
                //return false;
              });
            }

          }
        },
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            //console.log('Cancel clicked');
            return;
          }
        }
      ],
      enableBackdropDismiss: false
    });
    confirmPopup.present();
  }

  clearLocalStorage = () => {
    let confirmPopup = this.alertCtrl.create({
      title: 'Clear Local Storage',
      message: 'This will clear all data from Local Storage.  Are you sure you want to do this?',
      buttons: [
        {
          text: 'Yes',
          handler: () => {
            localStorage.clear();
            // TODO -  clear images if required - see /app/strongloop/loopback-clientservices-osc-images/client/js/deviceCtrl.js           
            //this.app.getRootNav().setRoot(MyApp);

            let alert = this.alertCtrl.create({
              title: 'Restart App',
              subTitle: 'Please restart the app.',
              buttons: ['OK'],
              enableBackdropDismiss: false
            });
            alert.present();

            //return false;
          }
        },
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            //console.log('Cancel clicked');
            return;
          }
        }
      ],
      enableBackdropDismiss: false
    });
    confirmPopup.present();
  }

  localStorageUsage = () => {
    var len = localStorage.length;
    var localStorageList = {};
    for (var i = 0; i < len; i++) {
      var key = localStorage.key(i);
      var value = localStorage[key];
      if (key == 'osc-local-db' || key == 'signature')		// Ignore (potentially very large) list of jobs etc.
        continue;
      localStorageList[key] = value;
    }
    return (localStorageList);
  }



  viewChangeData = () => {
    this.navCtrl.push("ChangePage").catch((err) => {
      //this.log.info("Error pushing ChangePage:" + err);
    });
  }

  clearChangeData = () => {
    this.messageService.clearChangeData();
  }

  deleteOldJobs = () => {
    this.jobService.deleteOldJobs(0);
  }

  deleteOldJSEA = () => {
    this.jseaService.deleteOldData(0);
  }

  clearSODDate = () => {
    this.sodService.clearSODDate();

  }

  setSODDate = () => {
    this.sodService.setSODDate();
    this.localStorageList = this.localStorageUsage();
  }

	signatureThrottleChanged = (newvalue) => {
		localStorage.setItem('signatureThrottle', newvalue);
	}
}



